import React from 'react';
import Card from './Card';


const CardList = ({robots}) =>{
   
    const cardComponent = robots.map((user, i) => {
        return <Card key={i} 
                id={robots[i].id} 
                name={robots[i].name} 
                email={robots[i].email}
                />
    });

    const mystyle = {
        display:'flex',
        flexWrap: 'wrap',
        justifyContent: 'center'
    };
    return(
        <div style={mystyle}>
            {cardComponent}
        </div>
    );
}

export default CardList;

